 entidades es de incentivar a los productores, para utilizar los manejos de bienestar animal en sus producciones y dejar a un lado prácticas tradicionales e ineficientes en donde de una u otra manera hay maltrato animal por desconocimiento de la norma.")])]),_c('div',{staticClass:"col-lg-3 mb-4 mb-lg-0",attrs:{"data-aos":"fade-left"}},[_c('figure',[_c('img',{staticClass:"img-t m-auto",attrs:{"src":require("@/assets/curso/images/tema2/img_7.png"),"alt":"Imagen Decorativa"}})])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"titulo-segundo color-acento-contenido",attrs:{"id":"t_2_2","data-aos":"zoom-out-right"}},[_c('h2',[_vm._v("2.2 Buenas prácticas pecuarias")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row align-items-center mb-5"},[_c('div',{staticClass:"col-lg-4 mb-4 mb-lg-0",attrs:{"data-aos":"fade-right"}},[_c('figure',[_c('img',{staticClass:"img-t m-auto",attrs:{"src":require("@/assets/curso/images/tema2/img_9.png"),"alt":"Imagen Decorativa"}})])]),_c('div',{staticClass:"col-lg-8",attrs:{"data-aos":"fade-left"}},[_c('p',[_vm._v("Las Buenas prácticas pecuarias son los procedimientos, actividades, condiciones y controles que se aplican en las unidades de producción de especies pecuarias para disminuir los peligros físicos, químicos o biológicos con el objetivo de garantizar la inocuidad y trazabilidad de los alimentos y de igual forma ayudar a la seguridad de los trabajadores, bienestar animal y sostenibilidad ambiental. Al implementar estos procesos se obtiene control de todos ellos en cada una de las áreas de producción y esto lleva a mejorar la rentabilidad con la opción de comercializar los productos a nivel nacional e internacional.")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row justify-content-center align-items-center mb-3"},[_c('div',{staticClass:"col-lg-1